import { useState, useEffect, useRef, useCallback } from 'react';
import { getAllImages } from './galleryData';

const useFullscreenGallery = () => {
  const [fullscreenActive, setFullscreenActive] = useState(false);
  const [initialSlide, setInitialSlide] = useState(0);
  const fullscreenSwiperRef = useRef(null);
  const scrollPositionRef = useRef(0);
  const allImages = getAllImages();

  const openFullscreenGallery = useCallback((imageUrl) => {
    const imageIndex = allImages.findIndex(url => url === imageUrl);
    if (imageIndex !== -1) {
      scrollPositionRef.current = window.scrollY;
      setInitialSlide(imageIndex);

      document.body.style.position = 'fixed';
      document.body.style.width = '100%';
      document.body.style.top = `-${scrollPositionRef.current}px`;
      document.body.style.overflow = 'hidden';

      setFullscreenActive(true);
    }
  }, [allImages]);

  const closeFullscreenGallery = useCallback(() => {
    const scrollY = scrollPositionRef.current;
    document.body.style.position = '';
    document.body.style.width = '';
    document.body.style.top = '';
    document.body.style.overflow = '';

    requestAnimationFrame(() => {
       window.scrollTo(0, scrollY);
    });


    setFullscreenActive(false);
  }, []);

  useEffect(() => {
    const handleKeyDown = (e) => {
      if (!fullscreenActive) return;

      if (e.key === 'Escape') {
        closeFullscreenGallery();
      } else if (fullscreenSwiperRef.current?.swiper) {
        if (e.key === 'ArrowLeft') {
          fullscreenSwiperRef.current.swiper.slidePrev();
        } else if (e.key === 'ArrowRight') {
          fullscreenSwiperRef.current.swiper.slideNext();
        }
      }
    };

    window.addEventListener('keydown', handleKeyDown);

    return () => {
      window.removeEventListener('keydown', handleKeyDown);

      if (document.body.style.position === 'fixed') {
         document.body.style.position = '';
         document.body.style.width = '';
         document.body.style.top = '';
         document.body.style.overflow = '';
      }
    };
  }, [fullscreenActive, closeFullscreenGallery]);

  return {
    fullscreenActive,
    initialSlide,
    allImages,
    fullscreenSwiperRef,
    openFullscreenGallery,
    closeFullscreenGallery,
  };
};

export default useFullscreenGallery;