import React from 'react';
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import 'swiper/css/autoplay';
import 'swiper/css/keyboard';
import 'swiper/css/mousewheel';
import './GallerySection.scss';

import FullscreenGallery from './components/FullscreenGallery';
import GalleryHeader from './components/GalleryHeader';
import GallerySlider from './components/GallerySlider';
import useFullscreenGallery from './useFullscreenGallery';

const GallerySection = () => {
  const {
    fullscreenActive,
    initialSlide,
    allImages,
    fullscreenSwiperRef,
    openFullscreenGallery,
    closeFullscreenGallery,
  } = useFullscreenGallery();

  return (
    <>
      <section className="gallery-section">
        <div className="gallery-section__container">
          <GalleryHeader />
          <GallerySlider onImageClick={openFullscreenGallery} />
        </div>
      </section>

      <FullscreenGallery
        active={fullscreenActive}
        initialSlide={initialSlide}
        images={allImages}
        onClose={closeFullscreenGallery}
        swiperRef={fullscreenSwiperRef}
      />
    </>
  );
};

export default GallerySection;